import logo from './logo.svg';
import './App.css';
import Receiver from './reciever';
import React from 'react'
class App extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      Receiver : new Receiver("CWAPerformanceMetrics"),
      data : []
    }
  }
  render(){
  
  return (
    <div className="App">
      <header className="App-header">
      <button onClick={() => {
        this.state.Receiver.getLatestItem().then((newData)=>this.setState({data: newData}))
        }}> Fetch </button>
      </header>

    </div>
  );
  }
}

export default App;
