import AWS from "aws-sdk"
AWS.config.update({
    'region':'us-east-1',
    'secretAccessKey':  process.env.API_KEY,
    'accessKeyId': process.env.API_KEY_ID
})
// ENDS HERE
const LATEST_HASH= "LatestHash"
const CWAData = "CWAData"
class Receiver{
    constructor(DataBaseName){
        this.dyanamoClient = new AWS.DynamoDB({apiVersion: '2012-08-10'});
        this.DataBaseName = DataBaseName
        this.CWAData = null
        this.latestHash = null
    }

    //update
    async update(){
        // check the latest hash from cache
        let DynamotHash = (await this.getLatestItem())["Hash"].S
        let cacheLatestItem = this.cacheGetLatestItem()//["Hash"].S
        console.log(cacheLatestItem)
        if (cacheLatestItem == undefined){
          console.log("NO cache found")
          // no cache found, pull every thing and set
          this.CWAData =  await this.getAllItems()
          this.latestHash = DynamotHash
          this.cacheSaveData()
        }
        // ask dynamo what is the lastest hash it received 
        // if hashes dont match call getBatchItem with local hash and update local hash with new data
    }
    //get latest item
    async  getLatestItem(){
      //add secondary index 
        const params = {
            // Set the projection expression, which are the attributes that you want.
            // ProjectionExpression: "Season, Episode, Title, Subtitle",
            TableName: this.DataBaseName,
            Limit: 1,
          };
          return (await this.dyanamoClient.scan(params).promise()).Items[0]
          // return this.latestItem
    }
    // get all
    async getAllItems(){
        const params = {
            // Set the projection expression, which are the attributes that you want.
            // ProjectionExpression: "Season, Episode, Title, Subtitle",
            TableName: this.DataBaseName,
            // Limit: 1,
          };
          return (await this.dyanamoClient.scan(params).promise()).Items
    }
    // get a single item
    getItem(hash) {
        var params = {
            TableName:  this.DataBaseName,
            Key: {
              'hash': {S:hash},
            },
          };
          console.log("reached here",params)
          // Call DynamoDB to read the item from the table
          this.dyanamoClient.getItem(params, function(err, data) {
            if (err) {
              console.log("Error", err);
            } else {
              console.log("Success", data.Item);
            }
          });
    }
    // get a batch of items
    /*
    lastHash : the last hash i have cached
    */
    // getBatchItem(lastHash){
    //     //
    //     var params = {
    //         RequestItems: {
    //           'TABLE_NAME': {
    //             Keys: [
    //               {'KEY_NAME': {N: 'KEY_VALUE_1'}},
    //               {'KEY_NAME': {N: 'KEY_VALUE_2'}},
    //               {'KEY_NAME': {N: 'KEY_VALUE_3'}}
    //             ],
    //             ProjectionExpression: 'KEY_NAME, ATTRIBUTE'
    //           }
    //         }
    //       };
          
    //       ddb.batchGetItem(params, function(err, data) {
    //         if (err) {
    //           console.log("Error", err);
    //         } else {
    //           data.Responses.TABLE_NAME.forEach(function(element, index, array) {
    //             console.log(element);
    //           });
    //         }
    //       });
    // }
    cacheGetLatestItem(){
      return localStorage.getItem(LATEST_HASH)
    }
    cacheSaveData(){
      localStorage.setItem(LATEST_HASH,this.latestHash)
      localStorage.setItem(CWAData,this.CWAData)

      console.log(localStorage.key(2))
    }
    
}


export default Receiver

